.collection-item {
    width: 21vw;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position:relative;
  
    .image {
      width: 100%;
      height: 95%;
      background-size: cover;
      background-position: center;
      margin-bottom: 5px;
    }

    .custom-button {
      width: 100%;
      opacity: 0.7;
      position: absolute;
      top: 255px;
      //2 above features determine that the button is position always 255px from top=== at the bottom of the image, so also added position: relative to collection-item container
      display: none; //so that button apears only on hover; look below added hover feature
    }
    
    &:hover {
      .image {
        opacity: 0.8;
      }
      .custom-button {
        opacity: 0.85;
        display: flex;
      }
    }
  
    .collection-footer {
      width: 100%;
      height: 5%;
      display: flex;
      justify-content: space-between;
      font-size: 18px;
  
      .name {
        width: 90%;
        margin-bottom: 15px;
      }
  
      .price {
        width: 10%;
      }
    }
  }
  